<template>
  <el-card id="box-card">
    <el-dialog title="" :visible.sync="dialogAddCourse" width="600px">
      <el-form :model="dialogForm" label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="dialogForm.pid"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogForm.tname"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogForm.time"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogForm.num"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogForm.grade"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddCourse = false"> </el-button>
        <el-button type="primary" @click="submit_add"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogChangeCourse" width="600px">
      <el-form :model="changeForm" label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="changeForm.pid"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="changeForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="changeForm.tname"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="changeForm.time"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="changeForm.num"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="changeForm.grade"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogChangeCourse = false"> </el-button>
        <el-button type="primary" @click="submit_change(changeForm)"> </el-button>
      </div>
    </el-dialog>
    <my-bread level1="" level2=""></my-bread>
    <el-row class="search_row">
      <el-col :span="6">
        <el-input placeholder="" v-model="query" class="input-with-search"></el-input>
      </el-col>
      <el-col :span="2" :offset="15">
        <el-button type="primary" size="small" @click="dialogAddCourse = true"></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
          :data="tableData"
          stripe
          :height="457"
          size="mini"
          style="width: 100%"
          @sort-change="sortpid"
          :default-sort="{prop: 'pid', order: 'descending'}"
        >
          <el-table-column type="index" width="50" align="center"></el-table-column>
          <el-table-column prop="pid" label="" sortable="custom" width="100" align="center"></el-table-column>
          <el-table-column prop="name" label="" width="150" align="center"></el-table-column>
          <el-table-column prop="tname" label="" width="120" align="center"></el-table-column>
          <el-table-column prop="time" label="" width="150" align="center"></el-table-column>
          <el-table-column prop="num" label="" width="120" align="center"></el-table-column>
          <el-table-column prop="lnum" label="" width="120" align="center"></el-table-column>
          <el-table-column prop="grade" label="" width="200" align="center"></el-table-column>
          <el-table-column label="" align="center" width="150">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row style="margin-top:15px">
      <el-col :span="24">
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="paginations.page_index"
            :page-sizes="paginations.page_sizes"
            :page-size="paginations.page_size"
            :layout="paginations.layout"
            :total="paginations.total"
          ></el-pagination>
        </div>
      </el-col>
    </el-row>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      query: "", // 
      dialogAddCourse: false, // 
      dialogChangeCourse: false, // 
      sort: "descending", // 
      searchTimer: null, // 
      dialogForm: {
        //  
        pid: "",
        name: "",
        tname: "",
        grade: "",
        time: "",
        num: "",
        lnum: ""
      },
      changeForm: {
        // 
        pid: "",
        name: "",
        tname: "",
        grade: "",
        time: "",
        num: "",
        lnum: ""
      },
      paginations: {
        // 
        page_index: 1,
        total: 0,
        page_size: 10,
        page_sizes: [10, 15, 20],
        layout: "total, sizes, prev, pager, next, jumper"
      },
      course: [], // 
      tableData: [], // 
      formLabelWidth: "120px" // 
    };
  },

  created() {
    this.getCourses("", this.sort, "pid"); //  
  },

  watch: {
    // 
    query: function(val, oldval) {
      const that = this;
      if (this.searchTimer != null) clearTimeout(this.searchTimer); // 
      this.searchTimer = setTimeout(function() {
        that.getCourses(val);
      }, 500);
    }
  },

  methods: {
    getCourses(keyword, sort, sortItem) {
      //
      this.$http
        .get("/users/allcourses", {
          params: {
            // 
            page_index: this.paginations.page_index, // 
            page_size: this.paginations.page_size, // 
            keyword, // 
            sort, // 
            sortItem // 
          }
        })
        .then(res => {
          this.tableData = res.data.data; // 
          this.paginations.total = res.data.total; // 
        })
        .catch(err => console.log(err));
    },

    submit_add() {
      // 
      this.dialogAddCourse = false;
      this.$http.post("/users/addcourse", this.dialogForm).then(res => {
        this.$message({
          type: "success",
          message: ""
        });
        this.getCourses("", this.sort, "pid"); // 
      });
    },

    submit_change() {
      // 
      this.dialogChangeCourse = false;
      this.$http.post("/users/changecourse", this.changeForm).then(res => {
        this.$message({
          type: "success",
          message: ""
        });
        this.getCourses("", this.sort, "pid"); // 
      });
    },

    // 
    handleSizeChange(page_size) {
      //  
      this.paginations.page_index = 1; //  
      this.paginations.page_size = page_size;
      this.getCourses("", this.sort, "pid");
    },

    handleCurrentChange(page) {
      //  
      this.paginations.page_index = page;
      this.getCourses("", this.sort, "pid");
    },

    // 
    sortpid(data) {
      this.paginations.page_index = 1;
      this.getCourses("", data.order, data.prop);
    },

    handleEdit(index, data) {
      // 
      this.changeForm._id = data._id;
      this.changeForm.pid = data.pid;
      this.changeForm.name = data.name;
      this.changeForm.tname = data.tname;
      this.changeForm.num = data.num;
      this.changeForm.grade = data.grade;
      this.changeForm.time = data.time;
      this.dialogChangeCourse = true;
    },

    handleDelete(index, data) {
      // 
      if (!confirm("")) {
        return;
      }
      this.$http
        .delete("/users/deletecou", {
          params: {
            _id: data._id
          }
        })
        .then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.getCourses("", this.sort, "pid");
        });
    },

    debounce(fn, time) {
      // 
      let timeout = null;
      return function() {
        if (timeout != null) clearTimeout(timeout);
        timeout = setTimeout(fn, time);
      };
    }
  }
};
</script>

<style>
#box-card {
  height: 100%;
  border: none;
}

.search_row {
  line-height: 60px;
  margin: 20px 0 20px 0;
}

#dialogAddStudent {
  width: 200px;
}

#bold {
  color: black;
  font-weight: bold;
}
</style>