<template>
  <el-card id="box-card">
    <el-dialog title="" :visible.sync="dialogAddTeacher" width="600px">
      <el-form :model="dialogForm" label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="dialogForm.pid"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogForm.course"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddTeacher = false"> </el-button>
        <el-button type="primary" @click="submit_add"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogChangeTeacher" width="600px">
      <el-form :model="changeForm" label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="changeForm.pid"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="changeForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="changeForm.name"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogChangeTeacher = false"> </el-button>
        <el-button type="primary" @click="submit_change(changeForm)"> </el-button>
      </div>
    </el-dialog>
    <my-bread level1="" level2=""></my-bread>
    <el-row class="search_row">
      <el-col :span="6">
        <el-input placeholder="" v-model="query" class="input-with-search"></el-input>
      </el-col>
      <el-col :span="2" :offset="15">
        <el-button type="primary" size="small" @click="dialogAddTeacher=true"></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
          :data="tableData"
          stripe
          :height="457"
          size="mini"
          style="width: 100%"
          @sort-change="sortpid"
          :default-sort="{prop: 'pid', order: 'descending'}"
        >
          <el-table-column type="selection" width="60" align="center"></el-table-column>
          <el-table-column type="index" width="60" align="center"></el-table-column>
          <el-table-column prop="pid" label="" sortable="custom" width="200" align="center"></el-table-column>
          <el-table-column prop="name" label="" width="200" align="center"></el-table-column>
          <el-table-column prop="course" label="" width="200" align="center"></el-table-column>
          <el-table-column prop="firstpwd" label="" width="200" align="center"></el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row style="margin-top:15px">
      <el-col :span="24">
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="paginations.page_index"
            :page-sizes="paginations.page_sizes"
            :page-size="paginations.page_size"
            :layout="paginations.layout"
            :total="paginations.total"
          ></el-pagination>
        </div>
      </el-col>
    </el-row>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      query: "",	// 
			dialogAddTeacher: false, // 
			dialogChangeTeacher: false, // 
			sort: 'descending',   // 
			searchTimer: null,   // 
      dialogForm: {  //  
        pid: "",
        name: "",
        grade: ""
			},
			changeForm: {	// 
				pid: "",
        name: "",
        grade: ""
			},
      paginations: {	// 
        page_index: 1,
        total: 0,
        page_size: 10,
        page_sizes: [10, 15, 20],
        layout: "total, sizes, prev, pager, next, jumper"
      },
      teacherData: [], // 
      tableData: [], // 
      formLabelWidth: "120px", // 
    }
	},
	
  created () {
    this.getTeachers('',  this.sort, 'pid')  //  
	},

	watch: { // 
    query: function(val, oldval) { 
			const that = this
			if(this.searchTimer != null) clearTimeout(this.searchTimer) // 
			this.searchTimer = setTimeout(function(){
				that.getTeachers(val)
			}, 500)
			
		}
	},
	
  methods: {
    getTeachers(keyword, sort, sortItem) { //
      this.$http
        .get("/users/allteacher", {
          params: { // 
            page_index: this.paginations.page_index,  // 
            page_size: this.paginations.page_size,    // 
            keyword, 																	// 
						sort,																			// 
            sortItem																	// 
          }
        })
        .then(res => {
          this.tableData = res.data.data  						// 
          this.paginations.total = res.data.total 		// 
        })
        .catch(err => console.log(err))
		},
		
    submit_add() { // 
      this.dialogAddTeacher = false
      this.$http.post("/users/addteacher", this.dialogForm).then(res => {
				this.$message({
					type: 'success',
					message: ''
				})
				this.getTeachers('', this.sort, 'pid')             // 
			})
		},

		submit_change () {	// 
			this.dialogChangeTeacher = false
			this.$http.post('/users/changeteacher', this.changeForm).then(res => {
				this.$message({
					type: 'success',
					message: ''
				})
				this.getTeachers('', this.sort, 'pid')             // 
			} )
		},
		
    // 
    handleSizeChange (page_size) {               //  
      this.paginations.page_index = 1						//  
      this.paginations.page_size = page_size
      this.getTeachers('', this.sort, 'pid')
		},
		
    handleCurrentChange (page) {									//  
      this.paginations.page_index = page
      this.getTeachers('', this.sort, 'pid')
		},
		
    // 
    sortpid (data) { 
      this.paginations.page_index = 1
      this.getTeachers("", data.order, data.prop)
		},
		
    handleEdit (index, data) { // 
			this.changeForm._id = data._id
      this.changeForm.pid = data.pid
      this.changeForm.name = data.name
      this.changeForm.course = data.course
			this.dialogChangeTeacher = true	
		},

		handleDelete (index, data) { // 
			if(!confirm('')){
				return
			}
			this.$http.delete ('/users/deletetea', {
				params:{
					_id: data._id
				}
			}).then(res => {
				this.$message({
						message: '',
						type: 'success'
				})
				this.getTeachers('', this.sort, 'pid')
			})
		},

		debounce (fn, time) {  // 
			let timeout = null
			return function () {
				if (timeout != null) clearTimeout(timeout)
				timeout = setTimeout(fn, time)
			}
		}
	}
}
</script>

<style>
#box-card {
  height: 100%;
  border: none;
}

.search_row {
  line-height: 60px;
  margin: 20px 0 20px 0;
}

#dialogAddStudent {
  width: 200px;
}

#bold {
	color: black;
	font-weight: bold;
}
</style>