<template>
  <el-card id="box-card">
    <el-dialog title="" :visible.sync="dialogAddStudent" width="600px">
      <el-form :model="dialogForm" label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="dialogForm.pid"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="dialogForm.grade" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddStudent = false"> </el-button>
        <el-button type="primary" @click="submit_add"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogChangeStudent" width="600px">
      <el-form :model="changeForm" label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="changeForm.pid"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="changeForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="changeForm.grade" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogChangeStudent = false"> </el-button>
        <el-button type="primary" @click="submit_change(changeForm)"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="updatestudent" width="600px">
      <el-upload
        name="studentexcel"
        class="upload-demo"
        action="http://localhost:3000/api/users/studentupdate"
        multiple
      >
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip"> .xlsx </div>
      </el-upload>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updatestudent = false"> </el-button>
        <el-button type="primary" @click="submit_update"> </el-button>
      </div>
    </el-dialog>
    <my-bread level1="" level2=""></my-bread>
    <el-row class="search_row">
      <el-col :span="6">
        <el-input placeholder="" v-model="query" class="input-with-search"></el-input>
      </el-col>
      <!-- <el-col :span="2" :offset="13">
        <el-button type="success" size="small" @click="updatestudent=true"></el-button>
      </el-col>-->
      <el-col :span="2" :offset="15">
        <el-button type="primary" size="small" @click="dialogAddStudent=true"></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table
          :data="tableData"
          stripe
          :height="457"
          size="mini"
          style="width: 100%"
          @sort-change="sortpid"
          :default-sort="{prop: 'pid', order: 'descending'}"
        >
          <el-table-column type="selection" width="60" align="center"></el-table-column>
          <el-table-column type="index" width="60" align="center"></el-table-column>
          <el-table-column prop="pid" label="" sortable="custom" width="200" align="center"></el-table-column>
          <el-table-column prop="name" label="" width="200" align="center"></el-table-column>
          <el-table-column prop="grade" label="" width="200" align="center"></el-table-column>
          <el-table-column prop="firstpwd" label="" width="200" align="center"></el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row style="margin-top:15px">
      <el-col :span="24">
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="paginations.page_index"
            :page-sizes="paginations.page_sizes"
            :page-size="paginations.page_size"
            :layout="paginations.layout"
            :total="paginations.total"
          ></el-pagination>
        </div>
      </el-col>
    </el-row>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      query: "", // 
      dialogAddStudent: false, // 
      dialogChangeStudent: false, // 
      updatestudent: false, // 
      sort: "descending", // 
      searchTimer: null, // 
      dialogForm: {
        //  
        pid: "",
        name: "",
        grade: ""
      },
      changeForm: {
        // 
        pid: "",
        name: "",
        grade: ""
      },
      paginations: {
        // 
        page_index: 1,
        total: 0,
        page_size: 10,
        page_sizes: [10, 15, 20],
        layout: "total, sizes, prev, pager, next, jumper"
      },
      studentData: [], // 
      tableData: [], // 
      formLabelWidth: "120px" // 
    };
  },

  created() {
    this.getStudents("", this.sort, "pid"); //  
  },

  watch: {
    // 
    query: function(val, oldval) {
      const that = this;
      if (this.searchTimer != null) clearTimeout(this.searchTimer); // 
      this.searchTimer = setTimeout(function() {
        that.getStudents(val);
      }, 500);
    }
  },

  methods: {
    getStudents(keyword, sort, sortItem) {
      //
      this.$http
        .get("/users/allstudent", {
          params: {
            // 
            page_index: this.paginations.page_index, // 
            page_size: this.paginations.page_size, // 
            keyword, // 
            sort, // 
            sortItem // 
          }
        })
        .then(res => {
          this.tableData = res.data.data; // 
          this.paginations.total = res.data.total; // 
        })
        .catch(err => console.log(err));
    },

    submit_add() {
      // 
      this.dialogAddStudent = false;
      this.$http.post("/users/addstudent", this.dialogForm).then(res => {
        this.$message({
          type: "success",
          message: ""
        });
        this.getStudents("", this.sort, "pid"); // 
      });
    },

    submit_change() {
      // 
      this.dialogChangeStudent = false;
      this.$http.post("/users/changestudent", this.changeForm).then(res => {
        this.$message({
          type: "success",
          message: ""
        });
        this.getStudents("", this.sort, "pid"); // 
      });
    },

    submit_update() {
      //  excel 
      this.updatestudent = false;
      this.getStudents("", this.sort, "pid");
    },

    // 
    handleSizeChange(page_size) {
      //  
      this.paginations.page_index = 1; //  
      this.paginations.page_size = page_size;
      this.getStudents("", this.sort, "pid");
    },

    handleCurrentChange(page) {
      //  
      this.paginations.page_index = page;
      this.getStudents("", this.sort, "pid");
    },

    // 
    sortpid(data) {
      this.paginations.page_index = 1;
      this.getStudents("", data.order, data.prop);
    },

    handleEdit(index, data) {
      // 
      this.changeForm._id = data._id;
      this.changeForm.pid = data.pid;
      this.changeForm.name = data.name;
      this.changeForm.grade = data.grade;
      this.dialogChangeStudent = true;
    },

    handleDelete(index, data) {
      console.log(data);
      let _this = this;
      // 
      if (!confirm("")) {
        return;
      }
      this.$http
        .post("/users/deletestu", {
          id: data._id
        })
        .then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.getStudents("", _this.sort, "pid");
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "",
            type: "error"
          });
        });
    },

    debounce(fn, time) {
      // 
      let timeout = null;
      return function() {
        if (timeout != null) clearTimeout(timeout);
        timeout = setTimeout(fn, time);
      };
    }
  }
};
</script>

<style>
#box-card {
  height: 100%;
  border: none;
}

.search_row {
  line-height: 60px;
  margin: 20px 0 20px 0;
}

#dialogAddStudent {
  width: 200px;
}
</style>